@use "../../assets/sass/variables" as *;

.navbar {
  border: 3px solid plum;
  background: rebeccapurple;
  background-attachment: scroll;
  color: white;
  position: fixed;
  text-align: center;
  border-radius: 6%;
  &__text,
  &__range {
    background: rebeccapurple;
  }
}

label {
  background: rebeccapurple;
}
